import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ParkList, StateList } from '../menu/menu.component'


@Component({
  selector: 'app-np-main',
  templateUrl: './np-main.component.html',
  styleUrls: ['./np-main.component.css']
})
export class NpMainComponent implements OnInit {
  name: string;
  np: {
    nameCh: string,
    nameEn: string,
    states: string[][],
    npsUrl: string,
  };

  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.name = this.route.snapshot.params['name'];
    let parkList = new ParkList(); 
    let stateList = new StateList();

    let state = parkList.getState(this.name);
    let stateNames = stateList.getName(state);

    this.np = {
      nameCh: parkList.getNameCh(this.name),
      nameEn: parkList.getNameEn(this.name),
      states: stateNames,
      npsUrl: parkList.park.get(this.name).getNpsUrl(),
    }
  }

}
