import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.css']
})
export class MenuComponent implements OnInit {
  parkList = new ParkList();
  menuStyle = MenuStyle;
  style: MenuStyle;

  constructor() {}

  ngOnInit() {
    this.style = this.menuStyle.TABLE;
  }

  changeMenuStyle() {
    if (this.style == this.menuStyle.TABLE) {
      this.style = this.menuStyle.GRID;
    } else {
      this.style = this.menuStyle.TABLE;
    }
  }

}

export enum MenuStyle {
  GRID,
  TABLE,
}

export class Park {
  urlName: string;
  npsUrl: string;
  
  constructor(name: string = '', npsUrl: string = '') {
    this.urlName = name;
    this.npsUrl = npsUrl;
  }

  getUrlName() {
    return this.urlName;
  }

  getNameShortCh() {
    return this.urlName;
  }

  getNameCh() {
    return this.urlName;
  }

  getNameShortEn() {
    return this.urlName;
  }

  getNameEn() {
    return this.urlName;
  }

  getStateShort() {
    return this.urlName;
  }

  getStateCh() {
    return this.urlName;
  }

  getStateEn() {
    return this.urlName;
  }

  getNpsUrl() {
    return `https://www.nps.gov/${this.npsUrl}/index.htm`;
  }
}

